/* 코인 css */

.appCoin h1 {
  margin-top: 20px;
  margin-bottom: 20px;
}

.coin-title {
  margin: 10px;
  margin-top: 30px;

  display: flex;
  flex-direction: column;
  width: 600px;
}

.coin-title .text-area {
  display: flex;
  width: 100%;
  font-size: 20px;
}

.button-area {
  display: flex;
  width: 100%;
  justify-content: flex-end;
}

.trade {
  padding: 10px;
  width: 100%;
  height: 150px;

  display: flex;
  /* width: 100%; */
  /* height: 100%; */
  flex-direction: row;
  /* 가로정렬 */
  justify-content: center;
  /* 세로정렬 */
  /* align-items: center; */
}

.trade button {
  margin-top: 5px;
  width: 70px;
  height: 30px;
  color: antiquewhite;
  border-radius: 3px;
  border: 0px;
}

.bgColorRed {
  background-color: crimson;
}

.bgColorBlue {
  background-color: royalblue;
}

.colorRed {
  color: crimson;
}

.colorBlue {
  color: royalblue;
}

.trade-type {
  flex: 20;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.trade-type .desc-area {
  display: flex;
  flex-direction: row;
}

.trade-type .desc-area .text-area {
  flex: 3;
  font-size: 12px;
}

.trade-type .desc-area .input-area {
  flex: 2;
  text-align: right;
}

.trade-type .desc-area .input-area input {
  z-index: 10;
  width: 100%;
  height: 100%;
}

.spare {
  flex: 1;
}